/* Extra small devices (phones, 500px and down) */
@media only screen and (max-width: 600px) {
  .cardBody {
    width: 100%;
  }
  .container-fluid {
    padding-right: 5px;
    padding-left: 5px;
  }
}
@media only screen and (min-width: 601px) {
  .cardBody {
    width: 200px;
  }

  .container-fluid {
    padding-right: 15px;
    padding-left: 15px;
  }
}

