.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.conditions {
  color: rgb(185, 8, 176);
}
.cardBody {
  border-radius: 18px;
  /* font-weight: bold; */
  font-size: medium;
  color: #1b1c1f;
  /* width: 200px; */
  padding: 5px;
  /* margin-right: 35px; */
  margin-top: 5px;
  margin-bottom: 5px;
  background-color: #f3e0e0a1;
  /* min-height: 100px; */
  box-shadow: 3px 3px red, -1em 0 0.4em olive;
}

.cardBody:hover {
  background-color: #e0d2ce;
  box-shadow: 3px 3px red, -1em 0 0.4em olive;
  transform: translateY(-7px);
}

.cardSubtitle {
  font-weight: bold;
  font-size: medium;
  color: #04164b;
  text-align: center;
}

.button {
  width: 100%;
  /* height: 45px; */
  /* font-family: "Roboto", sans-serif; */
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 2.5px;
  font-weight: 500;
  /* color: #000; */
  /* background-color: #fff; */
  border: none;
  border-radius: 45px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease 0s;
  cursor: pointer;
  outline: none;
}

